<template>
  <upload :uploadType="`head`" :imgWidth="`85px`" :imgHeight="`104px`" :imgUrl="imgUrl"
          @upload="getImgUrl"></upload>
</template>

<script>
  import upload from "@/components/public/upload"
    export default {
      name: "index",
      data(){
        return{
          imgUrl:""
        }
      },
      components:{
        upload
      },
      methods:{
        //接收子组件emit的事件
        getImgUrl(data) {
          //data  得到的就是返回的图片路径的相关参数
          console.log(data)
        }
      }
    }
</script>

<style scoped>

</style>



