<template>
  <div id="eplayer">
    <div>
      <a-player :music="songList" theme="#b7daff" v-if="flag" ref="player"></a-player>
    </div>

  </div>
</template>

<script>
  import VueAplayer from 'vue-aplayer'
  import {MUSIC} from "@/api/url"
  import imgurl from "../../assets/img/seminar/seminar.png"

  export default {
    components: {
      //别忘了引入组件
      'a-player': VueAplayer
    },
    data () {
      return {
        flag:true,
        musicList:'',
        songList:{
          title: 'Preparation',
          author: 'Hans Zimmer/Richard Harvey',
          url: "",
          pic: imgurl,
          lrc: '[00:00.00]lrc here\n[00:01.00]aplayer'
        },
        data:{}
      }
    },
    created() {
      // console.log(this.$route.params.id);
      this.songList.url=MUSIC+this.$route.params.id+'.mp3';
    }
  }
</script>

<style scoped>
</style>



