<template>
  <div id="orderComments">
    <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
    <div class="strong-line"></div>
    <h3 class="tip-box">满意度回访</h3>
    <div class="e-field">
      <p class="question-title-txt">首先，请您根据自己的感受对以下两道题目进行评分，10分表示“非常严重”，1分表示“非常轻微”，评分可以是10到1分的任意分数。</p>
      <p class="question-txt">1、在您前来咨询的时候，是如何评价您问题的困扰程度的？</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[0]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[0]}}分</div>
        </mt-range>
      </div>
      <p class="question-txt">2、现在，您是如何评价您问题的困扰程度的？</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[1]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[1]}}分</div>
        </mt-range>
      </div>
      <div class="line"></div>
      <p class="question-title-txt">接下来，请您对我们的服务评分。</p>
      <p class="question-txt">1、咨询师帮助您更有效地应对心理困扰。</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[2]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[2]}}分</div>
        </mt-range>
      </div>
      <p class="question-txt">2、咨询师及时对您的情感和困惑进行了回应。</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[3]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[3]}}分</div>
        </mt-range>
      </div>
      <p class="question-txt">3、如果您将来还需要心理上的帮助，您会继续选择我们的咨询
        服务。</p>
      <div class="range-box">
        <mt-range   v-model="rangeValue[4]"
                    :min="0"
                    :max="10"
                    :step="1"
                    :disabled="false"
                    :bar-height="6">
          <div slot="start"></div>
          <div slot="end" class="goal">{{rangeValue[4]}}分</div>
        </mt-range>
      </div>
      <h3 class="question-title-txt">4、在整个过程中，我们的服务让您最满意的是什么？</h3>
      <div class="area-box">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="e-field">
      <h3 class="question-title-txt">5、您对我们的服务还有什么期待或建议？</h3>
      <div class="area-box">
        <textarea class="area-input" placeholder=""></textarea>
      </div>
    </div>
    <div class="btn-box">
      <button class="closeBtn" @click="goTo('/consult/orderDetail/flag')">确认提交</button>
    </div>

  </div>
</template>

<script>
  import appHeader from '@/components/public/appHeader';
  export default {
    name: "record3",
    data(){
      return{
        showTitle:'个案记录',
        rangeValue:[8,8,8,8,8],
      }
    },
    created(){
    },
    watch:{
    },
    computed:{
    },
    components:{
      appHeader
    },
    methods:{
      backUp(){
        this.$refs.child.backUp();
      },
    }
  }
</script>

<style scoped lang="less">

  .tip-box{
    border-bottom: 1px solid #ededed;
  }

  .btnBox{
    width: 100%;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    padding-bottom: 28px;
  }
  .question-title-txt{
    font-size:24px;
    font-weight:400;
    color:rgba(84,84,84,1);
    line-height:36px;
    padding: 28px 36px;
    text-align: justify;

  }
  .question-txt{
    padding: 0 36px;
    text-align: justify;
    font-size:24px;
    font-weight:400;
    color:rgba(84,84,84,1);
    line-height:36px;
  }
  .range-box{
    padding: 0 36px;
    width: 600px;
    /*margin: 0 auto;*/
    margin-top: 10px;
    margin-bottom: 28px;
  }
  .goal{
    font-size: 30px;
    color: #4facfe;
    padding-left: 10px;
  }

  .btn-box{
    text-align: center;
    padding: 30px;
  }
</style>




