<template>
  <mt-search class="search-default"
    v-model="value"
    cancel-text="取消"
    placeholder="请输入姓名或关键字">
  </mt-search>
</template>

<script>
    export default {
        name: "search",
        data(){
          return {
            value:""
          }
        },
    }
</script>

<style scoped lang="less">
  .search-default{
    border-radius: 56px;
    background-color: #F4F4F4;
  }
  .mint-searchbar-inner{
    border-radius: 56px;
    background-color: #F4F4F4;
  }
</style>



