<template>
    <div id="archives">
      <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
      <div class="strong-line2"></div>
      <div class="select-box">
        <div class="select-list">
          <div class="archives-item" v-for="(item,index) in archivesList">
            <router-link to="/consult/updateArchives" class="archive">{{item.label}}</router-link>
            <button class="delete-btn" @click="deleteArchives(index)">删除</button>
          </div>
        </div>
        <div class="add-btn-box">
          <router-link class="add-btn" to="/consult/addArchives"><img src="../../assets/img/consult/add.png" alt=""><span class="add-txt">添加咨询人</span></router-link>
        </div>
      </div>
    </div>
</template>

<script>
  import appheader from "@/components/public/appHeader.vue"

    export default {
        name: "archives",
        data(){
          return{
            showTitle:"来访者档案",
            archivesList:[
              {
                label: '陈勤进 男 36岁 武汉 13978659842',
                value: "0"
              },
              {
                label: '王富贵 男 36岁 武汉 13978659842',
                value: "1"
              },
              {
                label: '牛东傲 男 36岁 武汉 13978659842',
                value: "2"
              }
            ],
            userData:[
              {name:"陈勤进"},
              {name:"王富贵"},
              {name:"牛东傲"}
            ]
            ,
            valueChoose:''
          }
        },
        components:{
          appHeader:appheader
        },
      methods:{
        backUp () {
          this.$refs.child.backUp();
        },
        deleteArchives(index){
          this.archivesList.splice(index,1)
        }
      },watch: {
        // valueChoose (val) {
        //   let pname=this.userData[val].name;
        //   this.$store.commit("setName",pname);
        //   let that=this;
        //   let timer= setTimeout(function () {
        //     that.$router.go(-1);
        //     clearTimeout(timer);
        //   },200);
        // }
      }
    }
</script>

<style scoped lang="less">
  .select-box{
    padding-top: 8px;
    .archives-item{
      position: relative;
      border-bottom: 1px solid #ededed;
      .archive{
        display: block;
        font-size:30px;
        font-weight:400;
        color:rgba(84,84,84,1);
        padding: 28px 36px;
        /*border-bottom: 1px solid #ededed;*/
      }
      .delete-btn{
        background: url("../../assets/img/mine/shanchu.png") no-repeat left center;
        background-size: 30px 30px;
        color: #4facfe;
        font-size: 30px;
        width: 126px;
        height: 30px;
        line-height: 30px;
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
      }
    }
    .add-btn-box{
      margin-top: 32px;
      text-align: center;
      .add-btn{
        img{
          width: 36px;
          height: 36px;
          vertical-align: sub;
          margin-right: 30px;
        }
        .add-txt{
          font-size:30px;
          color:#4facfe;
          line-height:54px;
        }
      }
    }
  }
</style>



