import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store=new Vuex.Store(
  {
      state:{
        count:132,
        person:{
          name:"张智超"
        },
        eapCommend:[
          {
            name:"盛达",
            level:"国家二级心理咨询师",
            speciality: "情绪管理、个人成长"
          },
          {
            name:"盛达2",
            level:"国家二级心理咨询师",
            speciality: "情绪管理、个人成长"
          }
        ]
      },
      mutations:{
        setName(state,name){
          state.person.name=name;
        },
        add(state){
          state.count=state.count+1
        }
      }

  });
export default store;



