<template>
  <div id="updateArchives">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="line"></div>
    <div class="tip-box">
      <h3 class="">咨询人信息</h3>
      <p>建议填写真实信息,有助于保障你的合法权益</p>
    </div>
    <div class="e-field">
      <div class="field-item m-disable">
        <label for="role">身份</label>
        <select name="" id="role" disabled>
          <option>请选择</option>
          <option>员工</option>
          <option>员工父母</option>
          <option>员工子女</option>
          <option>员工配偶</option>
        </select>
      </div>
      <mt-field label="姓名" :class="'m-disable'" :disabled="'disabled'" placeholder="请输入真实姓名" v-model="username"></mt-field>
      <mt-field label="工号" :class="'m-disable'" :disabled="'disabled'" placeholder="请输入公司工号" type="tel" v-model="jobnumber"></mt-field>
      <mt-field label="部门" placeholder="请输入部门" v-model="department"></mt-field>
      <div class="field-item m-disable">
        <label>性别</label>
        <label for="male">
          <input type="radio" name="sex" value="male" id="male" disabled>
          <span>男</span>
        </label>
        <label for="female">
          <input type="radio" name="sex" value="female" id="female" disabled>
          <span>女</span>
        </label>
      </div>
      <div class="field-item m-disable">
        <label for="year">出生年份</label>
        <select name="" id="year" disabled>
          <option>请选择</option>
          <option v-for="item in year">{{item}}</option>
          <!--<option>1990</option>-->
          <!--<option>1991</option>-->
          <!--<option>1992</option>-->
          <!--<option>1993</option>-->
          <!--<option>1994</option>-->
          <!--<option>1995</option>-->
          <!--<option>1996</option>-->
          <!--<option>1997</option>-->
          <!--<option>1998</option>-->
          <!--<option>1999</option>-->
        </select>
      </div>
      <div class="field-item">
        <label for="edu">学历</label>
        <select name="" id="edu">
          <option>请选择</option>
          <option>高中及以下</option>
          <option>中专</option>
          <option>大专</option>
          <option>本科</option>
          <option>硕士研究生</option>
          <option>博士及以上</option>
        </select>
      </div>
      <div class="field-item">
        <label for="marry">婚姻情况</label>
        <select name="" id="marry">
          <option>请选择</option>
          <option>已婚</option>
          <option>未婚</option>
          <option>离异</option>
          <option>丧偶</option>
          <option></option>
        </select>
      </div>
      <mt-field label="联系电话" :class="'m-disable'" :disabled="'disabled'" placeholder="请填写手机号码" type="tel" v-model="phone1"></mt-field>
      <mt-field label="备用电话" placeholder="请填写手机号码（选填）" type="tel" v-model="phone2"></mt-field>
      <div class="field-item">
        <label for="city">目前居住城市</label>
        <button @click="showArea" id="city" class="show-btn">{{btnTxt}}</button>
        <div class="pop-box" v-show="popupVisible">
          <button @click="hideArea" class="close-btn">确认</button>
          <eap-area v-on:childEvent="changeCity"></eap-area>
        </div>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="tip-box">
      <h3 class="">紧急联系人</h3>
      <p>
        在危急情况下,咨询师需要以下信息以确保你和他人 <br>
        安全,请务必如实填写; <br>
        未成年人需要填写监护人联系方式; <br>
        若信息不实,咨询师有权拒绝此次咨询预约.
      </p>
    </div>
    <div class="e-field">
      <mt-field label="姓名" placeholder="必填" v-model="linkman"></mt-field>
      <mt-field label="电话" placeholder="必填" type="tel" v-model="linkphone"></mt-field>
      <div class="field-item">
        <label for="relac">关系</label>
        <select name="" id="relac">
          <option>请选择</option>
          <option>父母</option>
          <option>子女</option>
          <option>配偶/伴侣</option>
          <option>朋友</option>
          <option>同事</option>
          <option>其他</option>
        </select>
      </div>
    </div>
    <div class="btn-box">
      <router-link to="/consult/selectArchives"><div class="submit-btn">完成</div></router-link>
    </div>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader.vue";
  import eapArea from "@/components/public/area.vue";

  export default {
    name: "addArchives",
    data(){
      return{
        showTitle:"编辑档案",
        year:[],
        username:"",
        phone1:"",
        phone2:"",
        department:"",
        jobnumber:"",
        linkman:"",
        linkphone:"",
        birthday:'',
        selectData: {
          defaultIndex: 0, //默认选中的是第几个
          selectStatus: false, // 通过selectStatus来控制下拉框的显示/隐藏
        },
        popupVisible:false,
        btnTxt:"请选择"
      }
    },
    components:{
      appHeader,
      eapArea
      // oSelect,
      //注册组件
    },
    created:function(){
      let i=1930;
      while (i<=2019){
        this.year.push(i);
        i++;
      }
      console.log(this.year);
    },
    methods:{
      backUp:function () {
        this.$refs.child.backUp();
      },
      showArea(){
        this.popupVisible=true;
      },
      hideArea(){
        this.popupVisible=false;
      },
      changeCity(data){
        console.log(data);
        this.btnTxt=data;
      }
    }
  }
</script>

<style scoped lang="less">
  .tip-box{
    width: 100%;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 20px;
    h3{
      font-size: 30px;
      line-height: 80px;
      font-weight: 400;
    }
    p{
      font-size: 24px;
      color: #b1b1b1;
      line-height: 36px;
    }
  }
  .field-item{
    height: 84px;
    width: 714px;
    margin-left: 50px;
    font-size: 30px;
    line-height: 60px;
    border-bottom: 1px solid #eee;
    padding-top: 10px;
    label{
      float: left;
      width: 220px;
      font-size: 30px;
    }
    input[type='radio']{
      width: 30px;
      height: 30px;
      overflow: visible;
      background: url("../../assets/img/consult/radiobBg2.png") no-repeat center;
      background-size: 30px 30px;
      vertical-align: sub;
      border: 0;
      outline: 0;
      -webkit-appearance: none;
    }

    input:checked{
      position: relative;
      background: #fff;
    }

    input:checked::after {
      position: absolute;
      background: url("../../assets/img/consult/radioicon.png") no-repeat center;
      background-size: 30px 30px;
      content: "";
      width: 30px;
      height: 30px;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
    }
  }
  .btn-box{
    text-align: center;
    .submit-btn{
      width:678px;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      border-radius:10px;
      font-size: 36px;
      color: #fff;
      margin: 36px auto;
      line-height: 100px;
    }
  }
  .show-btn{
    appearance: none; /* 去掉默认图标 */
    -moz-appearance: none; /* Firefox */
    -webkit-appearance: none; /* Safari and Chrome */
    background: url("../../assets/img/consult/downArrow.png") no-repeat scroll 95% center transparent ;/* 自己的图*/
    background-size: 18px 10px;
    /* 以下是圆角和边框等其他设置： */
    width:430px;
    height:60px;
    line-height:28px;
    border:0;
    outline: none;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    padding:0 20px 0 10px;
    text-align: left;
    color: #545454;
  }
  .pop-box{
    animation: slideInUp .5s;
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.7);
    z-index: 9;
    left: 0;
    bottom: 0;
    .close-btn{
      background: #fff;
      width: 100%;
      height: 60px;
      position: absolute;
      font-size: 30px;
      left: 0;
      bottom: 353px;
      color: #4facfe;
      text-align: right;
      padding-right: 36px;
      border-radius: 10px 10px 0px 0px;
    }
  }
  @-webkit-keyframes slideInUp {
    from{
      bottom:-100%;
    }
    to{
      bottom:0;
    }
  }

</style>





