<template>
  <div id="addOrder">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="line"></div>
    <div class="e-field">
      <div class="field-item">
        <label for="time">预约时间:</label>
        <span class="time-txt">{{dateFormat(orderTime)==dateFormat(startTime)?'未来14天可预约时间':dateFormat(orderTime)}}</span>
        <button class="order-time" @click="openPicker">选择时间</button>
        <mt-datetime-picker
          id="time"
          ref="picker"
          v-model="pickerValue"
          type="datetime"
          :start-date="startTime"
          @confirm="handleConfirm">
        </mt-datetime-picker>
      </div>

      <div class="field-item">
        <label>请选择咨询方式:</label>
        <ul class="way-list">
          <li class="way-item" :class="{ 'active': indexClass===index,'grey': disableWay==index}" v-for="(item,index) in itemList" :key="index" @click="getItem(index)">
            {{item.text}}
          </li>
        </ul>
      </div>
      <div class="field-item">
        <label>请选择咨询类型:</label>
        <ul class="way-list">
          <li class="way-item" :class="{ 'active': wayClass===index,'grey': disableWay==index}" v-for="(item,index) in wayList" :key="index" @click="getWayItem(index)">
            {{item.text}}
          </li>
        </ul>
      </div>
      <div class="field-item">
        <label>请选择咨询人:</label>
        <span class="consult-people">{{this.$store.state.person.name}}</span>
        <router-link to="/consult/selectArchives" class="order-time">添加/更换</router-link>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="tip-box">
      <h3 class="">以下困惑您的问题是</h3>
      <div class="chk-box">
        <label  v-for="(item,index) in questionList" :key="index">
          <input type="radio" :value="item" name="question">
          {{item}}
        </label>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="bg-box">
      <h3 class="home-title">推荐咨询师 <span class="refresh" @click="refreshBtn">换一换</span></h3>
    </div>
    <div class="line"></div>
    <ul class="eap-list shadow">
      <li class="eap-item" v-for="(item,index) in eapData">
          <img class="eap-pic" src="../../assets/img/home/eap.png" alt="">
          <div class="eap-info">
            <h3 class="eap-name">{{item.name}}</h3>
            <h4 class="eap-level">{{item.level}}</h4>
            <h5 class="eap-speciality">擅长：{{item.speciality}}</h5>
          </div>
      </li>
    </ul>
    <div class="strong-line2"></div>
    <div class="tip-box">
      <h3 class="bb0">主要问题/求助原因（必填）</h3>
      <div class="main-question">
        <mt-field placeholder="请简单描述您需要咨询的内容，以及期待达到的咨询目标。" v-model="username" type="textarea"></mt-field>
      </div>
    </div>

    <div class="data-box">
      <p><input type="checkbox"> 同意<router-link class="book" to="/notification">《知情通知书》</router-link></p>
    </div>
    <router-link to="/consult/orderList">
      <div class="btn-box">
        <button class="submit-btn">提交</button>
      </div>
    </router-link>
  </div>
</template>


<script>
  import appHeader from "@/components/public/appHeader.vue"

  export default {
    name: "addOrder",
    data(){
      return{
        showTitle:"咨询订单",
        username:"",
        phone:"",
        birthday:'',
        selectData: {
          defaultIndex: 0, //默认选中的是第几个
          selectStatus: false, // 通过selectStatus来控制下拉框的显示/隐藏
        },
        pickerValue:"",
        orderTime:new Date(),
        startTime:new Date(),
        indexClass:-1,
        itemList: [{text:"语音咨询"},{text:"视频咨询"},{text:"面对面"}],
        wayList: [{text:"个体"},{text:"家庭"}],
        questionList:["情绪管理","婚姻家庭","亲密关系","亲子关系","人际关系","个人成长","行为问题","职业发展","性心理"],
        eapData:[],
        a:true,
        disableWay:2,
        wayClass:""
      }
    },
    created(){
        this.eapData.push(this.$store.state.eapCommend[0])
    },
    components:{
      appHeader:appHeader,
      // oSelect,
      //注册组件
    },
    methods:{
      refreshBtn(){
        let that=this;
        if(that.a){
          that.eapData.pop();
          setTimeout(function () {
            that.eapData.push(that.$store.state.eapCommend[1]);
          },200);
          this.a=!this.a;
        }else {
          that.eapData.pop();
          setTimeout(function () {
            that.eapData.push(that.$store.state.eapCommend[0]);
          },200);
          this.a=!this.a;
        }
      },
      backUp:function () {
        this.$refs.child.backUp();
      },
      openPicker() {
        this.$refs.picker.open();
      },
      handleConfirm(data){
        this.orderTime = data;
      },
      //时间格式化函数，此处仅针对yyyy-MM-dd hh:mm:ss 的格式进行格式化
      dateFormat:function(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 在日期格式中，月份是从0开始的，因此要加0
         * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
      },
      getItem:function(index) {
        this.indexClass=index;// 把当前点击元素的index，赋值给activeClass
      },
      getWayItem:function(index) {
        this.wayClass=index;// 把当前点击元素的index，赋值给activeClass
      },
    }
  }




</script>

<style scoped lang="less">

  .data-box{
    padding: 0 50px 14px;
    p{
      font-size: 30px;
    }
    &.bb1{
      border-bottom: 1px solid #ededed;
    }
  }
  .book{
    color:#4facfe;
    font-size: 30px;
  }
  .tip-box{
    width: 100%;
    padding-bottom: 20px;
    h3{
      padding-left: 50px;
      padding-right: 50px;
      font-size: 30px;
      line-height: 80px;
      font-weight: 400;
      border-bottom: 1px solid #ededed;
    }
    .bb0{
      border-bottom: 0;
    }
  }
  .field-item{
    border-bottom: 1px solid #ededed;
    height: 96px;
    line-height: 96px;
    width: 750px;
    padding-left: 50px;
    font-size: 30px;
    label{
    float: left;
    /*width: 220px;*/
    font-size: 30px;
  }

}
  .btn-box{
    text-align: center;
    .submit-btn{
      width:678px;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      border-radius:10px;
      font-size: 36px;
      color: #fff;
      margin: 36px auto;
    }
  }
  .time-txt, .consult-people{
    font-size: 30px;
    color: #b1b1b1;
    margin-left: 10px;
  }
  .order-time{
    font-size:30px;
    font-weight:400;
    color:#4facfe;
    line-height:96px;
    float: right;
    margin-right: 40px;
  }
  .way-list{
    overflow:hidden;
    width: 440px;
    float: left;
    padding-top: 28px;
    .way-item{
      float: left;
      width:120px;
      height:40px;
      margin-left: 20px;
      border:2px solid rgba(84,84,84,1);
      border-radius:10px;
      color: #B1B1B1;
      font-size:24px;
      font-weight:400;
      color:rgba(84,84,84,1);
      line-height:40px;
      text-align: center;
      &.active{
        font-weight:400;
        color:#4facfe;
        border-radius:10px;
        border:2px solid #4facfe;
      }
      &.grey{
        font-weight:400;
        color:#ededed;
        border-radius:10px;
        border:2px solid #ededed;
      }
    }
  }

  .chk-box{
    padding-left: 50px;
    padding-right: 50px;
    label{
      display: inline-block;
      font-size: 30px;
      width: 180px;
      line-height: 80px;
      margin-right: 54px;
      &:nth-child(3n){
        margin-right: 0;
      }
    }
  }
  .main-question{
    width: 650px;
    margin: 0 auto;
    border: 2px solid #b1b1b1;
  }

  input[type='radio'], input[type='checkbox']{
    width: 38px;
    height: 38px;
    overflow: visible;
    background: url("../../assets/img/consult/radioBg.png") no-repeat center;
    background-size: 38px 38px;
    vertical-align: sub;
    border: 0;
    outline: 0;
    -webkit-appearance: none;
  }

  input:checked{
    position: relative;
    background: #fff;
  }

  input:checked::after {
    position: absolute;
    background: url("../../assets/img/consult/radioicon.png") no-repeat center;
    background-size: 38px 38px;
    content: "";
    width: 38px;
    height: 38px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }

  .bg-box{
    background-color: #fff;
    padding-left: 36px;
    padding-right: 36px;
  }
  .home-title{
    line-height:34px;
    font-size:30px;
    color: #545454;
    padding: 28px 0;
    font-weight: 400;
    .refresh{
      float: right;
      font-size:30px;
    }
  }
  .list-box{
    overflow: hidden;
    .list-item{
      float: left;
      width:200px;
      height:120px;
      margin-right: 38px;
      border-radius:20px;
      position: relative;
      &:nth-child(3n){
        margin-right: 0;
      }
      img {
        border-radius:20px;
      }
      .words{
        position: absolute;
        width:200px;
        height:30px;
        line-height: 30px;
        background:rgba(255,255,255,.5);
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        text-align: center;
        color: #545454;
      }
    }
  }
  .eap-list{
    overflow: hidden;
    width: 750px;
    height: 220px;
    .eap-item{
      padding-top: 24px;
      width: 100%;
      height: 206px;
      /*border-bottom: 1px solid #ededed;*/
      .eap-pic{
        box-sizing: content-box;
        float: left;
        width:157px;
        height:157px;
        margin: 0 56px 0 36px;
        border-radius:10px;
      }
      .eap-info{
        float: left;
        width: 500px;
        padding-right: 36px;
        overflow: hidden;
        .eap-name{
          color: #545454;
          font-weight: 400;
        }
        .eap-level,.eap-speciality{
          color: #b1b1b1;
          font-weight: 400;
          padding-top: 28px;
        }
      }
    }
  }
</style>





