     overflow: hidden;
     border-bottom: 1px solid #ededed;
     clear: both;
   }
   .read-item2{
     padding-bottom: 28px;
     .read-title{
       color: #545454;
       font-size: 30px;
       line-height: 45px;
       padding: 28px;
     }
     .read-img-box{
       .read-img{
         float: left;
         margin-right: 10px;
         width:220px;
         height:160px;
         border-radius:10px;
         &:first-child{
           margin-left: 36px;
         }
       }
     }
   }
 }
</style>



