<template>
	<div id="appHeader">
		<div class="header_left" @click.native="backUp">
			<i></i>
			<!--<span>返回</span>-->
		</div>
		<div class="header_title" v-text="title">
			
		</div>
	</div>

</template>
<script>
	export default{
		name:'appHeader',
		props:['title'],
		data () {
			return {
				
			}
		},
		methods:{
      backUp () {
        window.history.length > 1
          ? this.$router.go(-1)
          : this.$router.push('/')
      },
		}
	}
</script>
<style lang="less">
  #appHeader {
    width: 100%;
    height: 76px;
    position: relative;
    background: #fff;

    .header_left {
      position: absolute;
      left: 36px;
      top: 0;
      height: 100%;
      display: inline-block;
      font-size: 36px;
      width: 44px;

      i {
        display: inline-block;
        position: absolute;
        left: 0;
        top: 16px;
        background: url(../../assets/img/return.png) no-repeat center;
        background-size: 100% 100%;
        width: 24px;
        height: 44px;
      }
    }

    .header_title {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      font-size: 36px;
      line-height: 76px;
      text-align: center;
    }

  }

</style>



