<template>
    <div id="seminar">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="seminar-box">
        <ul class="seminar-list">
          <li class="seminar-item" v-for="(item,index) in seminarList" @click="goTo('/seminar/detail')">
            <div class="item-left">
              <h3>{{item.title}}</h3>
              <p>{{item.text.length<=32?item.text:(item.text.slice(0,32)+"...")}}</p>
            </div>
            <div class="item-right" @click.stop="playMusic(item.address)">
              <img :src=item.img alt="">
            </div>
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue";
  import src1 from "@/assets/img/seminar/seminar.png";
  import src2 from "@/assets/img/seminar/seminar2.png";
  import src3 from "@/assets/img/seminar/seminar3.png";


    export default {
        name: "index",
        data(){
          return{
            showTitle:"冥想专题",
            seminarList:[
              {img:src1,title:"高效睡眠-观想心湖",text:"最简单，最有效的放松减压方法——\n" +
                  "观呼吸。心烦、强迫、失眠来扰。……",address:"7af7b319ff1b7167"},
              {img:src2,title:"缓解身体疲惫：水晶球冥想",text:"最简单，最有效的放松减压方法——\n" +
                  "观呼吸。心烦、强迫、失眠来扰。……",address:"90f7f50e2757b72c"},
              {img:src3,title:"积极赋能，提升内在活力-树下冥想",text:"最简单，最有效的放松减压方法——\n" +
                  "观呼吸。心烦、强迫、失眠来扰。……",address:"efa658bcf719485c"},
            ]
          }
        },
      components:{
          appHeader
      },
      methods:{
          backUp(){
              this.$refs.child.$router.go(-1);
          },
          playMusic(address){
            this.$router.push({name:'player',params:{id:address}});
          }
      }
    }
</script>

<style scoped lang="less">
  #seminar{
    .seminar-box{
      .seminar-list{
        .seminar-item{
          padding: 24px 36px;
          border-bottom: 1px solid #ededed;
          width: 100%;
          height: 216px;
          overflow: auto;
          .item-left{
            float: left;
            width: 406px;
            h3{
              font-size: 30px;
              line-height: 60px;
              font-weight: 400;
            }
            p{
              font-size: 24px;
              color: #b1b1b1;
              line-height: 40px;
              height: 80px;
              overflow: hidden;
            }
          }
          .item-right{
            float: right;
            width: 220px;
            overflow: hidden;
            border-radius: 10px;
            position: relative;
            &:after{
              content: "";
              display: block;
              position: absolute;
              left: 0;
              right: 0;
              top: 0;
              bottom: 0;
              margin: auto;
              background: url("../../assets/img/seminar/btn.png") no-repeat center;
              background-size: 50px 50px;
            }
            img{
              width: 100%;
            }
          }
        }
      }
    }
  }

</style>



