<template>
    <div id="appfilter" class="appfilter">
      <mt-popup
        v-model="popupVisible"
        :modal="false"
        :closeOnClickModal="false"
        position="right" class="pop-box">
        从右侧弹出
        <button class="close-btn" @click="closePop">点击关闭</button>
      </mt-popup>
      <button class="btn" @click="showPop">点击弹出</button>
    </div>
</template>

<script>
  import Vue from 'vue';
  import { Popup } from 'mint-ui';

  Vue.component(Popup.name, Popup);
    export default {
        name: "appfilter",
        data(){
          return{
            "popupVisible":false
          }
        },
        methods:{
          showPop:function () {
            this.popupVisible=true
          },
          closePop:function () {
            this.popupVisible=false
          }
      }
    }

</script>

<style scoped>
  .pop-box{
    width: 100%;
    height: 80%;
    background-color: #fff;
  }
</style>



