<template>
  <div id="orderDetail">
    <app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>
    <div class="line"></div>
    <div class="eap-data">
      <h3>预约咨询师</h3>
      <div class="eap-pic">
        <img src="" alt="">
      </div>
      <div class="eap-state">
        <p class="eap-name">盛达</p>
        <p class="eap-way">语音咨询</p>
      </div>
    </div>
    <div class="strong-line2"></div>
    <div class="person-data">
      <h3>预约信息</h3>
      <div class="data-box">
        <p>咨询方式：语音咨询</p>
        <p>咨询工具：微信，12123123123</p>
        <p>咨询类型：个体</p>
        <p>姓名：张智超</p>
        <p>性别：男</p>
        <p>年龄：30岁</p>
        <p>预约时间：{{dateFormat(orderTime)}}</p>
        <p>问题类型：情绪管理</p>
        <p>问题描述：如何情绪管理</p>
        <p>紧急联系人：张超，13422818291</p>
        <!--<button class="time-btn" @click="openPicker">修改咨询时间</button>-->
      </div>
      <!--<mt-datetime-picker-->
        <!--id="time"-->
        <!--ref="picker"-->
        <!--v-model="pickerValue"-->
        <!--type="datetime"-->
        <!--:start-date="startTime"-->
        <!--@confirm="handleConfirm">-->
      <!--</mt-datetime-picker>-->
    </div>
    <div class="strong-line2"></div>
    <div class="order-data">
      <h3>订单信息</h3>
      <div class="data-box bb1">
        <p>创建时间：2019-03-20 10:10:34</p>
        <p>预约编号：1234567890</p>
        <p>更改订单时间：2019-03-21（周五）14:00-14:50</p>
        <div v-show="showUpdate=='cancel'">
          <p>取消订单时间：2019-03-21（周五）14:00</p>
          <p>取消订单原因：由于某某原因，取消预约（用户取消不显示）</p>
        </div>
      </div>
      <div class="tips-txt">
        温馨提示 1. 咨询开始的12小时之前可无责任取消订单，距离咨询开始的12小时内取消订单将占用您一次咨询机会，即一周内无法再次咨询。
        2. 如果您需要修改咨询时间和咨询方式，请于咨询开始的6小时之前点击“修改订单”按钮进行修改，且每单仅允许修改一次，6小时之内不允许修改订单。
        3. 如果有其他问题，请与人工客服（电话4006191990）联系。
      </div>
      <div class="data-box">
        <p>查看 <router-link class="book" to="/notification">知情通知书</router-link></p>
      </div>
    </div>
      <div class="btn-box" v-show="showUpdate=='showUpdate'">
        <router-link to="/consult/orderList" class="noway">取消订单</router-link>
        <router-link to="/consult/orderUpdate" class="ok">修改订单</router-link>
      </div>
  </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue";

  export default {
    name: "orderDetail",
    data() {
      return {
        showTitle: "订单详情",
        pickerValue:"",
        startTime:new Date(),
        orderTime:new Date(),
        showUpdate:this.$route.params.flag
      }
    },
    components: {
      appHeader
    },
    methods:{
      openPicker() {
        this.$refs.picker.open();
      },
      backUp () {
        this.$refs.child.backUp();
      },
      handleConfirm(data){
        this.orderTime = data;
      },
      //时间格式化函数，此处仅针对yyyy-MM-dd hh:mm:ss 的格式进行格式化
      dateFormat:function(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        /* 在日期格式中，月份是从0开始的，因此要加0
         * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
         * */
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day+" "+hours+":"+minutes+":"+seconds;
      },
    }
  }
</script>

<style scoped lang="less">
  #orderDetail{
    padding-bottom: 120px;
  }
  .btn-box{
    position: fixed;
    width: 100%;
    height: 100px;
    bottom: 0;
    left: 0;
    z-index: 9;
    margin: 0 auto;
    text-align: center;
    display: flex;
    .ok,.noway{
      flex: 1;
      line-height: 100px;
      margin: 0;
      padding: 0;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      font-size:36px;
      color:rgba(255,255,255,1);
    }
    .noway{
      background: #EDEDED;
      color: #545454;
    }
  }
  .eap-data{
    padding: 28px 36px;
    overflow: auto;
    h3{
      font-weight: 400;
      font-size: 30px;
      line-height: 84px;
    }
    .eap-pic{
      width:80px;
      height:80px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      border-radius:10px;
      margin-right: 40px;
      float: left;
    }
    .eap-state{
      float: left;
      .eap-name{
        font-size: 30px;
        margin-bottom: 18px;
      }
    }
  }
  .person-data,.order-data{
    h3{
      font-weight: 400;
      font-size: 30px;
      line-height: 84px;
      padding: 0 36px;
      border-bottom: 1px solid #ededed;
    }
    .data-box{
      position: relative;
      padding: 14px 36px 28px;
      p{
        line-height: 50px;
      }
      .time-btn{
        font-size:24px;
        color:#4facfe;
        position: absolute;
        top: 46%;
        right: 36px;
      }
    }
  }
  .order-data{
    .data-box{
      padding-bottom: 14px;
      &.bb1{
        border-bottom: 1px solid #ededed;
      }
    }
    .book{
      color:#4facfe;
    }
  }
</style>



