<template>
    <div id="mine">
      <!--<app-header :title="showTitle" @click.native="backUp" ref="child"></app-header>-->
      <div class="user-head">
        <div class="head-box">
          <img src="../../assets/img/mine/userhead.png" alt="">
        </div>
        <h4>匿名</h4>
      </div>
      <div class="strong-line2"></div>
      <div class="user-operate">
        <div class="op-item" @click="goTo('/consult/orderList')">
          <img src="../../assets/img/mine/wode.png" alt="">
          <span >我的咨询</span>
        </div>
        <div class="op-item">
          <img src="../../assets/img/mine/shoucang.png" alt="">
          <span >我的收藏</span>
        </div>
        <div class="op-item" @click="goTo('consult/archives')">
          <img src="../../assets/img/mine/laifangzhe.png" alt="">
          <span >来访者管理</span>
        </div>
        <div class="op-item">
          <img src="../../assets/img/mine/xuexi.png" alt="">
          <span >我的学习</span>
        </div>
      </div>
    </div>
</template>

<script>
  // import appHeader from "@/components/public/appHeader"
    export default {
        name: "mine",
        data(){
          return{
            showTitle:"个人中心"
          }
        },
        components:{
          // appHeader
        }
    }
</script>

<style scoped lang="less">
.user-head{
    width:750px;
    height:350px;
    background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
    padding-top: 1px;
  .head-box{
    width:120px;
    height:120px;
    background:rgba(84,84,84,1);
    border-radius:50%;
    overflow: hidden;
    margin: 96px auto 18px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  h4{
    font-weight: 400;
    font-size:30px;
    color:rgba(255,255,255,1);
    line-height:54px;
    text-align: center;
  }
}
  .user-operate{
    padding-top: 14px;
    width: 100%;
    .op-item{
      padding: 0 36px;
      width: 100%;
      height: 80px;
      line-height: 80px;
      background: url("../../assets/img/mine/xuanzhe.png") no-repeat 95% center;
      background-size: 14px 28px;
      border-bottom: 1px solid #ededed;
      img{
        vertical-align: sub;
        width: 30px;
        height: 30px;
        margin-right: 18px;
      }
      span{
        display: inline-block;
        padding-top: 3px;
        font-size: 30px;
      }
    }
  }
</style>



