import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/index/home.vue'
import index from '@/components/index/index.vue'
import consult from '@/components/consult/consult.vue'
import eapList from '@/components/consult/eapList.vue'
import eapDetail from '@/components/consult/eapDetail.vue'
import func from '@/components/func/func.vue'
import test from '@/components/test/test.vue'
import mine from '@/components/mine/mine.vue'
import appfilter from '@/components/public/appfilter.vue'
import selectArchives from "@/components/consult/selectArchives";
import addArchives from "@/components/consult/addArchives";
import archives from "@/components/consult/archives";
import updateArchives from "@/components/consult/updateArchives";
import addOrder from "@/components/consult/addOrder";
import orderList from "@/components/consult/orderList";
import orderDetail from "@/components/consult/orderDetail";
import orderUpdate from "@/components/consult/orderUpdate";
import orderComments from "@/components/consult/orderComments";
import appSelect from "@/components/public/appSelect";
import information from "@/components/information/index";
import detail from "@/components/information/detail";
import notification from '@/components/public/notification';
import area from "@/components/public/area";
import seminar from '@/components/seminar/index';
import seminarDetail from '@/components/seminar/detail';
import seminarPlayer from '@/components/seminar/player';
import upload from "@/components/public/upload";
import eap from "@/components/eap/index";


Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes:[
    {
      path:'',
      component:index,
      children:[
        {
          path:'/',
          component:home
        },
        {
          path:'consult',
          component:consult
        },
        {
          path:'func',
          component:func
        },
        {
          path:'test',
          component:test
        },
        {
          path:'mine',
          component:mine,
          meta: {
            requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path:'/information',
          component:information
        }
      ]

    },
    {
      path:'/consult/eapList',
      component: eapList
    },
    {
      path:'/consult/eapDetail',
      component: eapDetail
    },
    {
      path:'/consult/selectArchives',
      component: selectArchives
    },
    {
      path:'/consult/addArchives',
      component: addArchives
    },
    {
      path:'/consult/updateArchives',
      component: updateArchives
    },
    {
      path:'/consult/archives',
      component: archives
    },
    {
      path:'/consult/addOrder',
      component: addOrder
    },
    {
      path:'/consult/orderList',
      component: orderList
    },
    {
      path:'/consult/orderDetail/:flag',
      component: orderDetail
    },
    {
      path:'/appfilter',
      component: appfilter
    },
    {
      path:'/information',
      component: information,
      meta: {
        title: '咨讯首页'
      }
    },
    {
      path:'/appSelect',
      component: appSelect
    },
    {
      path:'/information/detail',
      component:detail
    },
    {
      path:'/notification',
      component:notification
    },
    {
      path:'/area',
      component:area
    },
    {
      path:'/seminar',
      component:seminar
    },
    {
      path:'/seminar/detail',
      component:seminarDetail
    },
    {
      name:'player',
      path:'/seminar/player',
      component:seminarPlayer
    },
    {
      path:'/upload',
      component:upload
    },
    {
      path:'/eap',
      component:eap
    },
    {
      path:'/consult/orderUpdate',
      component:orderUpdate
    },
    {
      path:'/consult/orderComments',
      component:orderComments
    },

  ]
})



