<script>
    import Vue from 'vue';
    import { Popup } from 'mint-ui';
    import appheader from '@/components/public/appHeader.vue'
    import search from '@/components/public/search.vue'

    Vue.component(Popup.name, Popup);
    export default {
      name: "eapList",
      data(){
        return{
          popupVisible:false,
          // indexClass:[{way:-1},{city:-1},{domain:-1},{sex:-1},{age:-1}],
          indexClass:[-1,-1,-1,-1,-1],
          itemList:[
            [{text:"全部"},{text:"北京市"},{text:"武汉市"},{text:"南京市"},{text:"深圳市"},{text:"杭州市"},{text:"广州市"},{text:"上海市"},{text:"青岛市"}],
            [{text:"不限"},{text:"男性咨询师"},{text:"女性咨询师"}],
            [{text:"不限"},{text:"语音咨询"},{text:"视频咨询"},{text:"面对面"}],
            [{text:"全部"},{text:"个人"},{text:"家庭"}],
            [{text:"婚姻家庭"},{text:"情绪管理"},{text:"恋爱心理"},{text:"职场心理"},{text:"人际关系"},{text:"亲子教育"},{text:"个人成长"},{text:"心理健康"},{text:"性心理"}],
            [{text:"儿童"},{text:"青少年"},{text:"中年人"},{text:"老年人"},{text:"孕产妇"},{text:"学生"},{text:"留学生"},{text:"职场人"},{text:"伴侣/夫妻"},{text:"LGBTQ"}],
          ],
          tabsParam:['心理','法律','理财','健康'],//（这个也可以用对象key，value来实现）
          nowIndex:0,//默认第一个tab为激活状态
        }
      },
      components:{
          'app-header':appheader,
          'search':search
      },
      methods:{
        backUp () {
          window.history.length > 1
            ? this.$router.go(-1)
            : this.$router.push('/')
        },
        showPop:function () {
          this.popupVisible=true
        },
        closePop:function () {
          this.popupVisible=false
        },
        getItem:function(index,activeClass) {
          this.indexClass.splice(activeClass,1,index);// 把当前点击元素的index，赋值给activeClass
        },
        resetData:function () {
          this.indexClass=[-1,-1,-1,-1,-1];
        },

        toggleTabs:function(index){
          this.nowIndex=index;
        },
      }
    }
</script>

