  .eap-list{
    overflow: hidden;
    width: 750px;
    .eap-item{
      padding-top: 24px;
      width: 100%;
      height: 206px;
      border-bottom: 1px solid #ededed;
      .eap-pic{
        box-sizing: content-box;
        float: left;
        width:157px;
        height:157px;
        margin: 0 56px 0 36px;
