    }
  }
  .eap-filter{
    width: 100%;
    height: 70px;
    border-bottom: 1px solid #ededed;
    .filter-btn{
      height:70px;
      line-height: 70px;
      float: right;
      margin-right: 36px;
      font-size:30px;
      font-weight:400;
      color:#4facfe;
      .down-arrow{
        float: right;
        margin-left: 10px;
        background: url("../../assets/img/consult/downArrow.png") no-repeat center;
        width: 18px;
        height: 70px;
        background-size: contain;
      }
    }
  }
  .pop-box{
    position: fixed;
    width: 100%;
    height: 86%;
    left: 100%;
    top: 182px;
    overflow-y: scroll;
    background-color: #fff;
    transition: left .3s ease-in-out;
    &.active{
      left: 0;
    }
    .pop-title{
      font-size: 30px;
      padding-top: 10px;
      line-height: 56px;
      padding-left: 64px;
      font-weight: 400;
    }
    .filter-list{
      overflow:hidden;
      width: 100%;
      border-bottom: 2px solid #ededed;
      padding-bottom: 14px;
      .filter-item{
        float: left;
        width:180px;
        height:50px;
        border:2px solid #B1B1B1;
        color: #B1B1B1;
        border-radius:25px;
        margin: 14px 32px 14px 36px;
        font-size:24px;
        font-weight:400;
        text-align: center;
        line-height: 50px;
        &.active{
          font-weight:400;
          color:#4facfe;
          border:2px solid #4facfe;
          border-radius:25px;
        }
      }
    }
    .close-btn,.reset-btn{
      width:375px;
      height:100px;
      background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
      font-size:36px;
      font-weight:400;
      color:rgba(255,255,255,1);
      float: right;
    }
    .reset-btn{
      float: left;
      color: #545454;
      background:rgba(237,237,237,1);
    }
  }
  /*tab*/
  .tabs{
    float: left;
    width: 600px;
    padding:0;
    font-weight:400;
    color:rgba(177,177,177,1);
    overflow: hidden;
    text-align: center;
  }
  .li-tab{
    width:120px;
    line-height: 70px;
    color: #B1B1B1;
    border-radius:20px;
    display:inline-block;
    text-align: center;
    margin: 0 12px;
    font-size: 30px;
  }
  .li-tab.active{
    color:#4facfe;
  }
</style>



