<template>
    <div id="seminarDetail">
      <app-header :title="showTitle" ref="child" @click.native="backUp"></app-header>
      <div class="pic">
        <img src="../../assets/img/seminar/banner.png" alt="">
      </div>
      <div class="pic-data">
        <div class="icon"><img src="../../assets/img/seminar/icon.png" alt=""></div>
        <div class="txt">
          <h3>大海的冥想</h3>
          <p>1.5万人订阅</p>
        </div>
        <div class="collect">收藏</div>
      </div>
      <div class="strong-line2"></div>
      <div class="introduce-box">
        <h3 class="introduce-tit">栏目介绍</h3>
        <p class="introduce-content">
          海之冥想，通过意象想象，在你的心理投下一个“锚”，让你在汹涌情绪中保持觉知和定力，在安全宣泄的同时，化解和他人的矛盾带来的负面影响。
        </p>
        <ul class="class-data">
          <li class="course">
            <span>课程</span>
            <p>2个</p>
          </li>
          <li class="time">
            <span>总时长</span>
            <p>60分钟</p>
          </li>
          <li class="personum">
            <span>参加人数</span>
            <p>5423人</p>
          </li>
        </ul>
        <div class="strong-line2"></div>
        <div class="course-list">
          <h3 class="course-tit">课程列表</h3>
          <div class="audio-list">
            <div class="audio-item">
              <span class="audio-index">1</span>
              <span class="audio-name">观呼吸</span>
              <span class="audio-time">时长：25:00</span>
              <span class="audio-play"></span>
            </div>
            <div class="audio-item">
              <span class="audio-index">2</span>
              <span class="audio-name">观呼吸</span>
              <span class="audio-time">时长：25:00</span>
              <span class="audio-play"></span>
            </div>
          </div>
        </div>
        <div class="strong-line2"></div>
        <div class="comments-box">
          <h3 class="comments-title">用户评论({{this.$store.state.count}})</h3>
          <div class="comments-item" v-for="(item,i) in txt">
            <div class="head-icon"><img src="../../assets/img/info/headicon.png" alt=""></div>
            <div class="comments-data">
              <h4>匿名</h4>
              <span>2019-2-18</span>
            </div>
            <div class="comments-content">
              {{item.length>maxLength?(item.substring(0, 67)+"……"):item}}
              <span v-if="item.length>maxLength" class="fold" @click="openTxt($event,i)">展开</span>
            </div>
          </div>
        </div>
        <div class="float-box">
          <textarea  class="float-area" ref="area" placeholder="万千思绪，下笔汇聚评论一句"></textarea>
          <div @click="submit"><input class="sub-btn" type="button" value="发表"></div>
        </div>

      </div>
    </div>
</template>

<script>
  import appHeader from "@/components/public/appHeader.vue"
    export default {
      name: "detail",
      data() {
        return{
          showTitle:"观呼吸",
          num:1234,
          maxLength:80,
          flag:true,
          txt:[
            "很感谢老师为我考虑，咨询过后，根据老师的建议做了一些改变。自己也开始感受到一股力量注入我的身体，使我向上,感受到自己的心被阳光照耀着我。自己也开始感受到一股力量注入我的身体",
            "很感谢老师为我考虑，咨询过后，根据老师的建议做了一些改变。自己也开始感受到一股力量注入我的身体，使我向上,感受到自己的心被阳光照耀着我。",
            "很感谢老师为我考虑，咨询过后，根据老师的建议做了一些改变。自己也开始感受到一股力量注入我的身体，使我向上,感受到自己的心被阳光照耀着我。自己也开始感受到一股力量注入我的身体，使我向上,感受到自己的心被阳光照耀着我。",
            "很感谢老师为我考虑，咨询过后，根据老师的建议做了一些改变。"
          ],
          dataId:[]
        }
      },
      components: {
        appHeader
      },
      methods:{
        backUp(){
          this.$refs.child.backUp();
        },
        submit(){
          // console.log(this.$refs.area.value);
          this.$refs.area.value="";
        },
        openTxt(e,i){
          console.log(i);
          let el=e.target.parentNode;
          el.innerHTML=this.txt[i];
          el.classList.add("showAll");
      }
    }
  }
</script>

<style scoped lang="less">
  #seminarDetail{
    padding-bottom: 116px;
    .pic{
      width: 750px;
      height: 320px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .pic-data{
      width: 750px;
      height: 180px;
      display: flex;
      align-items: center;
      .icon,.txt,.collect{
        flex-direction: column;
        flex-grow: 1;
      }
      .icon{
        width: 152px;
        height: 80px;
        padding: 0 36px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .collect{
        width:150px;
        height:60px;
        line-height: 60px;
        text-align: center;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border-radius:30px;
        color: #fff;
        font-size: 30px;
        position: relative;
        right: 36px;
      }
      .txt{
        width: 440px;
        h3{
          font-size: 30px;
          font-weight: 400;
          padding-bottom: 20px;
        }
        p{
          font-size: 24px;
          color: #b1b1b1;
        }
      }
    }
    .introduce-box{
      .introduce-tit{
        font-weight: 400;
        font-size: 30px;
        padding: 28px;

      }
      .introduce-content{
        font-size: 24px;
        color: #b1b1b1;
        line-height: 36px;
        padding: 0 28px;
        text-indent: 2em;
        text-align: justify;
        border-bottom: 1px solid #ededed;
        padding-bottom: 28px;
      }
      .class-data{
        width: 750px;
        height: 142px;
        display: flex;
        padding: 28px 36px;
        color: #545454;
        font-size: 24px;
        p{
          font-size: 30px;
          padding-top: 20px;
        }
        .course{
          flex-grow: 1;
          p{
            padding-left: 2px;
          }
        }
        .time{
          flex-grow: 1;
          text-align: center;

        }
        .personum{
          flex-grow: 1;
          text-align: right;
          p{
            padding-right: 2px;
          }
        }
      }
    }
    .course-list{
      width: 750px;
      height: 316px;
      .course-tit{
        font-weight: 400;
        font-size: 30px;
        padding: 36px 36px 16px;
      }
      .audio-item{
        height: 106px;
        border-top: 1px solid #ededed;
        display: flex;
        align-items: center;
        padding: 0 36px;
        .audio-index{
          display: inline-block;
          width:50px;
          height:50px;
          border:2px solid rgba(191,191,191,1);
          border-radius:50%;
          font-size:30px;
          color:rgba(84,84,84,1);
          text-align: center;
          line-height: 46px;
        }
        .audio-name{
          font-size: 24px;
          margin-left: 36px;
        }
        .audio-time{
          font-size: 18px;
          color: #b1b1b1;
          margin-left: 56px;
          position: relative;
          &:before{
            content: "";
            display: inline-block;
            position: absolute;
            background: url("../../assets/img/seminar/shijian.png") no-repeat center;
            width: 18px;
            height: 18px;
            background-size: 100%;
            top: 0;
            bottom: 0;
            left: -22px;
            margin: auto;
          }
        }
        .audio-play{
          background: url("../../assets/img/seminar/btn.png") no-repeat center;
          background-size: contain;
          width: 50px;
          height: 50px;
          margin-left: auto;
        }
      }
    }
    .comments-box{
      .comments-title{
        font-size: 30px;
        font-weight: 400;
        padding: 36px 36px 0;
      }
      .comments-item{
        padding-top: 28px;
        border-bottom: 1px solid #ededed;
        padding-bottom: 28px;
      }
      .head-icon{
        margin-left: 36px;
        margin-right: 30px;
        float: left;
        width:80px;
        height:80px;
        background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
        border-radius:50%;
        margin-bottom: 16px;
        img{
          max-width: 100%;
        }

      }
      .comments-data{
        color: #B1B1B1;
        float: left;
        h4{
          margin-top: 5px;
          margin-bottom: 20px;
        }
      }
      .comments-content{
        clear: both;
        width:658px;
        height:108px;
        line-height: 36px;
        text-align: justify;
        font-size:24px;
        color:rgba(191,191,191,1);
        margin: 0 auto;
        position: relative;
        &.showAll{
          height: auto;
        }
      }
      .fold{
        display: block;
        position: absolute;
        bottom: 0;
        right: 0;
        color: #4facfe;
      }
    }
    .float-box{
      width: 100%;
      height: auto;
      background: #fff;
      position: fixed;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      z-index: 9;
      text-align: center;
      .float-area{
        text-align: left;
        margin-bottom: 28px;
        width:678px;
        height:60px;
        border:2px solid rgba(191,191,191,1);
        border-radius:30px;
        font-size: 24px;
        line-height: 60px;
        padding-left: 74px;
        background: rgba(255,255,255,1) url("../../assets/img/info/edit.png") no-repeat 30px center;
        background-size: 22px 22px;
        outline: none;
        &:focus {
          height: 300px;
          background-position: 22px 20px;
        }
        &:focus+div .sub-btn{
          opacity: 1;
          margin-right: 36px;
          margin-bottom: 28px;
          width: 100px;
          height: 40px;
        }
      }
      .sub-btn{
        transition: all .01s;
        float: right;
        background: #ededed;
        outline: none;
        border: 0;
        border-radius: 5px;
        opacity: 0;
        width: 0;
        height: 0;
        margin: 0;
      }
    }
  }
</style>



