<template>
    <div id="notification">
      <h3 class='title'>心理咨询知情同意书</h3>
      <p v-html="notificationTxt">
      </p>
      <div class="btn-box">
        <button @click="backUp" class="returnBtn">关闭</button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "notification",
      data(){
        return{
          notificationTxt:
            "欢迎使用易普斯的EAP心理咨询服务。为了给您提供透明、安全、平等的咨询环境，促进在咨询前咨询师与来访者之间的有效沟通，来访者需要了解以下建立心理咨询关系将要涉及的一些基本问题。如有任何异议，请在咨询前及时与咨询师沟通商议。为了保障您的权益和咨询效果，在您阅读、理解并同意以下内容之后我们才能为您提供心理咨询服务。 <br>" +
            "<br>" +
            "<b>一、咨询流程说明</b><br>" +
            "1、 咨询时间和频次<br>" +
            "个人咨询时间50分钟/次，家庭治疗时间90分钟/次。如需连续咨询则至少间隔一周。<br>" +
            "<br>" +
            "2、 预约<br>" +
            "咨询前在APP上进行预约。如连续咨询，每次咨询之前仍需进行预约确认，如无提前预约则视作放弃继续咨询。<br>" +
            "<br>" +
            "3、 取消、违约及迟到<br>" +
            "咨询一经预约，来访者需根据约定的时间，准时赴约或通过电话联系，并确保自己在约定的时间内处于一个合适心理咨询的状态。<br>" +
            "如您需要更换时间，请提前联系工作人员，以便重新预约或取消咨询，面询提前一天、电询提前3小时告知。如没有提前告知工作人员，您将失去一次咨询机会（咨询次数的限制依据贵单位与我公司的合同约定）。如果您在咨询中迟到，咨询仍按约定时间结束。<br>" +
            "<br>" +
            "4、心理咨询的准备<br>" +
            "心理咨询效果有待于心理咨询师和来访者的共同努力，心理咨询是一个促使个人心灵成长的过程，可能也会带来一定的痛苦和不适感。所以，需要心理咨询的来访者，需要在时间、心理等方面都有相应的准备。<br>" +
            "<br>" +
            "5、 咨询环境<br>" +
            "请保持咨询环境的安静、保密和无人打扰。面询时尽量不要接听电话、出入咨询室，电询时不要在喧哗、非保密和有人在场的环境下进行，如您所处环境不适合电询，咨询师有权停止咨询，并建议另择咨询时间和环境。<br>" +
            "<br>" +
            "6、转介<br>" +
            "如咨询师对您的困扰评估，认为已超出我们的服务范围，需要其他更适宜的机构帮助，我们将提出转介建议。<br>" +
            "<br>" +
            "7、关于社交媒体的使用<br>" +
            "咨询师和来访者在没有必要的情况下互不添加为好友，互不开放社交媒体中有关个人生活的动态展示（如微博、微信朋友圈、QQ空间等），以保护来访者隐私不会在其不知情、不允许的情况被咨询师看到；同时也避免咨询师的空间因为对来访开放而造成不必要的双重关系（如咨询师做培训，来访者在咨询期间参加，成为培训师与学员关系等）。<br>" +
            "<br>" +
            "8、咨询的局限性<br>" +
            "心理咨询师不同于专业医疗机构的心理科/精神科医生，心理咨询师为来访者提供的是心理咨询服务。如果来访者需要服用药物，请咨询专业医疗机构的心理科/精神科医生。心理咨询师不会在药物使用的问题上提供专业指导。<br>" +
            "来访者有义务提供专业医疗机构做出的精神疾病相关诊断，不能隐瞒精神病史，有义务按照咨询师要求到专业医疗机构进行精神疾病鉴别诊断，在精神科医生指导下通过心理咨询进行辅助治疗。<br>" +
            "<br>" +
            "9、满意度问卷<br>" +
            "为了提高服务质量，我们将在咨询结束后对您的满意度进行调查，请您参与配合。<br>" +
            "<br>" +
            "10、 请咨访双方确保在咨询前已经认真阅读，并且承诺在咨询过程中会履行相应的责任和义务。易心理有权根据需要不时地制定、修改本协议和/或各类规则，如有任何变更，易心理将在网站、APP等以公示的方式进行公告，不再单独通知每个用户。变更后的协议和规则一经公布即自动生效，成为本协议的一部分。如用户不同意相关变更，应当立即以书面通知的方式终止本协议；任何修订和新规则一经在网站上公布即自动生效，成为本协议的一部分。如用户登陆或继续使用服务的，则视为对修改后的协议和规则不持异议并同意遵守。除另行明确声明外，任何使服务范围扩大或功能增强的新内容均受本协议约束。<br>" +
            "<br>" +
            "11、本协议之解释与适用，以及与本协议有关的争议，均应依照中华人民共和国法律予以处理，并以北京市海淀区人民法院为管辖法院。如本协议的任何条款被视作无效或无法执行，则上述条款可被分离，其余部分则仍具有法律效力。<br>" +
            "<br>" +
            "<b>二、心理咨询的保密原则</b><br>" +
            "1、 保密范围<br>" +
            "根据法律和职业道德的要求，咨询师会最大限度地为您在咨询中提供的信息保密；咨询过程中的个案记录、测验资料和其他注明保密的资料，我们将在严格保密的情况下进行保存。<br>" +
            "<br>" +
            "2、 何时打破保密<br>" +
            "如咨询过程中咨询师发现您（员工本人或者家属）有以下情况，将打破保密原则，并采取必要的措施防范意外事件发生，必要时会通知您的家人、咨询付费单位及相关人员、有关部门，转介到其他机构或与其他心理咨询专业人员进行磋商。<br>" +
            "1）有自身安全受到伤害或伤害他人的严重风险时；<br>" +
            "2）有致命的传染性疾病等且可能危及他人时；<br>" +
            "3）未成年人在受到性侵犯或虐待时；<br>" +
            "4）法律规定需要披露时。<br>" +
            "<br>" +
            "<b>三、来访者的权利</b><br>" +
            "1、 您有权获知咨询师的训练背景、学习经历，以决定接受或拒绝咨询。<br>" +
            "2、 咨询过程中，您有权知道自己目前的心理状况，有权了解咨询师在整个咨询过程中所使用的方法，并有权与咨询师探讨最适宜的方法。<br>" +
            "3、 如果咨询效果不显著，您有权提出要求更换咨询师。<br>" +
            "4、 未经您的允许，咨询师不得对咨询过程进行录音或录像。<br>" +
            "<br>" +
            "<b>四、来访者的义务</b><br>" +
            "1、 在咨询开始前认真阅读、理解并同意《心理咨询知情同意书》的内容。<br>" +
            "2、 遵守心理咨询的流程设置，尊重咨询师的专业工作，且与咨询师在咨询设置的保护下合作。<br>" +
            "3、以积极的态度对待咨询，坦诚地向咨询师表露自己，不掩饰或伪装。若因为来访者提供不实信息而影响咨询效果，责任不属于咨询师一方。<br>" +
            "4、 在咨询结束前不做出重大决定，如自杀、离婚、辞职等。<br>" +
            "<br>" +
            "<b>五、咨询师的权利</b><br>" +
            "1、 有权利了解与来访者心理问题有关的个人资料。<br>" +
            "<br>" +
            "2、 有权利选择合适匹配的来访者。<br>" +
            "<br>" +
            "3、本着对求助者负责的态度，当来访者情况超出咨询师个人能力处理的范畴时，有权与来访者沟通原因并在征得来访者同意后及时安排转介。<br>" +
            "<br>" +
            "<b>六、咨询师的义务</b><br>" +
            "1. 对咨询内容严格保密，对有关资料妥善保管，不在咨询以外谈论来访者。如果情况属于保密例外，咨询师可突破保密原则。<br>" +
            "<br>" +
            "2. 遵守职业道德，尊重民族、性别、宗教、文化等特点，遵守职业规则，遵守国家有关的法律法规。<br>" +
            "<br>" +
            "3. 避免双重关系，不得接受来访者的礼物，且不在咨询之外与来访者进行咨询性质的交谈。<br>" +
            "<br>" +
            "4. 应遵守《中华人民共和国精神卫生法》第二十三条：<br>" +
            "心理咨询人员不得从事心理治疗或者精神障碍的诊断、治疗。<br>" +
            "心理咨询人员发现接受咨询的人员可能患有精神障碍的，应当建议其到符合本法规定的医疗机构就诊。<br>"
        }
      },
      methods:{
        backUp(){
          this.$router.go(-1);
        }
      }
    }
</script>

<style scoped lang="css">
  p{
    padding:28px 36px;
    padding-bottom: 100px;
    font-size: 24px;
    text-align: justify;
  }
  p>>>b{
    font-size: 30px;
    font-weight: normal;
  }
  .title{
    padding-top: 30px;
    text-align: center;
    font-size: 30px;
  }
  .btn-box{
    text-align: center;
    position: fixed;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 0;
  }
  .returnBtn{
    width: 100%;
    height: 80px;
    font-size: 30px;
    background:linear-gradient(90deg,rgba(79,172,254,1),rgba(0,242,254,1));
    color: #fff;
    margin: 0 auto;
  }
</style>



